/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&family=Roboto:wght@400;900&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

*, *:before, *:after {
  box-sizing: border-box; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 12px; }
  @media (max-width: 992px) {
    .container {
      max-width: 720px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 100%; } }

.header {
  background-color: #AA0601; }
  .header .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (max-width: 767px) {
      .header .navbar {
        flex-direction: column;
        align-items: center; } }
  @media (max-width: 767px) {
    .header .container {
      max-width: 100%;
      padding: 0; } }
  .header img {
    width: 80px;
    padding: 12px 0; }
  .header .menu {
    display: flex; }
    .header .menu li {
      margin-left: 64px; }
    .header .menu a {
      display: block;
      text-decoration: none;
      color: #FFFF;
      font-family: "Roboto", "Noto Sans TC", sans-serif;
      font-size: 20px;
      line-height: 1.5; }
      .header .menu a:hover {
        color: gray; }
    @media (max-width: 767px) {
      .header .menu {
        width: 100%;
        flex-wrap: wrap; }
        .header .menu li {
          padding: 12px 0px;
          width: 50%;
          margin-left: 0px;
          border: 1px solid #FFFF; }
          .header .menu li a {
            font-size: 16px;
            text-align: center; } }

.footer {
  background-color: #AA0601;
  padding-top: 48px;
  padding-bottom: 32px; }
  .footer .linkbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 36px; }
  .footer .pagelink {
    display: flex;
    justify-content: space-between; }
    .footer .pagelink li {
      margin-right: 48px; }
      @media (max-width: 996px) {
        .footer .pagelink li {
          margin-right: 30px; } }
    .footer .pagelink a {
      font-family: "Roboto", "Noto Sans TC", sans-serif;
      font-size: 16px;
      line-height: 24px;
      font-weight: regular;
      text-decoration: none;
      color: #FFFFFF; }
      .footer .pagelink a:hover {
        color: gray; }
  .footer .socialmedia {
    display: flex; }
    .footer .socialmedia li {
      margin-left: 32px; }
      @media (max-width: 767px) {
        .footer .socialmedia li {
          margin-left: 8px; } }
      .footer .socialmedia li a {
        display: block; }
        .footer .socialmedia li a:hover {
          opacity: 0.5; }
      .footer .socialmedia li img {
        width: 48px; }
      @media (max-width: 996px) {
        .footer .socialmedia li img {
          width: 40px; } }
  @media (max-width: 767px) {
    .footer .linkbar {
      display: none; } }
  .footer .contact .socialmedia {
    display: none; }
  .footer .contact-text {
    padding-bottom: 32px;
    margin-bottom: 32px;
    border-bottom: 1px solid #ffffff; }
    .footer .contact-text a {
      text-decoration: none;
      color: #FFFFFF; }
      .footer .contact-text a:hover {
        opacity: 0.5; }
    .footer .contact-text .fa-phone, .footer .contact-text .fa-envelope {
      color: #FFFFFF;
      margin-right: 22px; }
      @media (max-width: 767px) {
        .footer .contact-text .fa-phone, .footer .contact-text .fa-envelope {
          margin-right: 10px; } }
    .footer .contact-text p {
      font-family: "Roboto", "Noto Sans TC", sans-serif;
      font-size: 36px;
      line-height: 54px;
      color: #FFFFFF; }
  @media (max-width: 767px) {
    .footer .contact {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #ffffff;
      margin: 0 15px 24px;
      padding: 0px; }
      .footer .contact .contact-text {
        padding-bottom: 0px;
        border: 0px; }
      .footer .contact p {
        font-size: 16px;
        line-height: 24px; }
      .footer .contact .socialmedia {
        display: flex; } }
  .footer .copyright-mobile {
    display: none; }
  .footer .copyright {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .footer .copyright p {
      font-family: "Roboto", "Noto Sans TC", sans-serif;
      font-size: 16px;
      line-height: 24px;
      color: #ffffff; }
    .footer .copyright ul {
      display: flex;
      justify-content: space-between; }
    .footer .copyright li {
      margin-left: 48px; }
    .footer .copyright a {
      text-decoration: none;
      font-family: "Roboto", "Noto Sans TC", sans-serif;
      font-size: 14px;
      line-height: 21px;
      color: #FFFFFF; }
      .footer .copyright a:hover {
        color: gray; }
  @media (max-width: 767px) {
    .footer .copyright {
      display: none; }
    .footer .copyright-mobile {
      display: block;
      margin: 0 15px 24px;
      padding: 0px; }
      .footer .copyright-mobile li {
        margin-bottom: 8px;
        color: #ffffff; }
      .footer .copyright-mobile a {
        text-decoration: none;
        font-family: "Roboto", "Noto Sans TC", sans-serif;
        font-size: 14px;
        line-height: 21px;
        color: #FFFFFF; }
        .footer .copyright-mobile a:hover {
          color: gray; } }

.QA {
  margin-top: 100px;
  margin-bottom: 80px; }
  .QA h2 {
    border-left: 8px solid #650300;
    padding-left: 16px;
    font-family: "Roboto", "Noto Sans TC", sans-serif;
